<template>
	<div id="openWechatPay">
		<!--未授权-------------isPremission==false------------------->
		<div class="noPremission" v-if="isPremission==false">
			<div class="img left">
				<img src="../image/emptypic_pic_normal.png" />
			</div>

			<div class="text left">
				<p class="bigTop">使用支付宝当面付，需先开通参数配置权限，请联系客服：</p>
				<p class="blue bigTop">0755-xxxxyyyy</p>
				<p>若您未在本平台内录入完整的工商和账户信息，您需要准备以下资料：</p>
				<p>1.营业执照注册号、经营范围、营业期限、营业执照扫描件</p>
				<p>2.法人代表姓名、法人手机号码、法人身份证正面和反面扫描件、身份证号码、身份证有效期</p>
				<p>3.《餐饮服务许可证》（《食品卫生许可证》）或者提供实体店铺的三张照片（包含：1. 门头照含店名；2. 店</p>
				<p>内全景照；3. 店内收银台照）须为彩色图片且小于2M，文件格式为bmp、png、jpeg、jpg或gif</p>
				<p>4.开户银行名称、开户名称、银行账号</p>
			</div>
		</div>

		<!--授权-->
		<div v-else>

			<!--未配置 ----------isConfig==false------------------>
			<div v-if="isConfig==false" class="notConfig">
				
				<el-form label-width="152px" :model="noConfigForm" label-position="right" ref="noConfigForm">
					<el-form-item label="">
						<p class="small">注：为确保支付宝支付正常使用，请确认支付宝官方后台 的回调页面域名填写正确！（回调页面域名：xinchihuo.xx.xx.com）</p>
					</el-form-item>
					<el-form-item label="支付宝APPID">
						<el-input v-model="noConfigForm.appId"></el-input>
						<p class="small">注：用来配置微信支付的身份标识</p>
					</el-form-item>
					<el-form-item label="RSA私钥">
						<el-input type="password" v-model="noConfigForm.passWord"></el-input>
						<p class="small">注：用来配置微信支付的授权验证</p>
					</el-form-item>
						<el-form-item label="支付宝公钥">
						<el-input type="password" v-model="noConfigForm.passWord"></el-input>
						<p class="small">注：用来配置微信支付的授权验证</p>
					</el-form-item>
					<el-form-item label="安全校验码">
						<el-input  v-model="noConfigForm.test"></el-input>
					</el-form-item>
					<el-form-item label="合作者身份">
						<el-input  v-model="noConfigForm.partner"></el-input>
					</el-form-item>
					<el-form-item label="支付宝账号">
						<el-input  v-model="noConfigForm.accountNum"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button class="submitBtn" @click="submitForm('noConfigForm')">提交验证</el-button>
						<el-button class="cancelBtn" @click="resetForm('noConfigForm')">重置</el-button>
					</el-form-item>
				</el-form>
			</div>

			<!--已配置-->
			<div v-else>
				<el-form label-width="152px" :model="noConfigForm" label-position="right" ref="noConfigForm">
					<el-form-item label="支付宝APPID">
						<span>{{noConfigForm.appId}}</span>
					</el-form-item>
					<el-form-item label="RSA私钥">
						<span>{{noConfigForm.passWord}}</span>
					</el-form-item>
					<el-form-item label="支付宝公钥">
						<span>{{noConfigForm.passWord}}</span>
					</el-form-item>
					<el-form-item label="安全校验码">
						<span>{{noConfigForm.test}}</span>
					</el-form-item>
					<el-form-item label="合作者身份">
						<span>{{noConfigForm.partner}}</span>
					</el-form-item>
					<el-form-item label="支付宝账号">
						<span>{{noConfigForm.accountNum}}</span>
					</el-form-item>
				</el-form>
			</div>

		</div>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				isPremission: false,
				isConfig: false,
				noConfigForm: {
					appId: "abchadho1232198jcna",//支付宝APPID
					passWord: " ",
					//RSA私钥
					test: "abchadho1232198jcna",//安全校验码
					partner:"abchadho1232198jcna",//合作者身份
					accountNum:"rick@morty.com"//支付宝账号
				}
			}
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#openWechatPay {
		margin-left: -45px;
	}
	
	.img {
		width: 50px;
		height: 50px;
		margin-right: 30px;
	}
	.text-header{
		padding-left: 75px;
	}
	img {
		width: 100%;
		height: 100%;
	}
	
	.text {
		width: 80%;
		margin-left: 35px;
	}
	
	p {
		font-size: 16px;
		height: 30px;
		line-height: 30px;
	}
	
	.small {
		font-size: 12px;
		color: darkgray;
	}
	
	.blue {
		font-size: 20px;
		color: #00CCFF;
	}
	
	.bigTop {
		height: 50px;
		line-height: 50px;
	}
	
	.notConfig {
		width: 60%;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#openWechatPay {
		.el-radio.yes {
			margin-right: 100px;
		}
		.el-button.submitBtn {
			margin: 0 30px 0 200px;
		}
		.el-button.cancelBtn {
			background: #fff;
			color: #4877E7;
		}
		.el-button.downLoad {
			background: #fff;
			color: #666666;
			border: 1px solid #666666;
		}
		.el-form-item__label {
			text-align: right;
			/*padding-left: 0;*/
			padding-right: 47px;
		}
	}
</style>


